h1 {
  margin: 0;
  padding: 0;
  font-size: 28px;
  text-align: center;
}
h2 {
  margin: 0;
  padding: 0;
  font-size: 14px;
  text-align: center;
}

body {
  font-family: Helvetica}

.facetName {
  padding-top: 20px;
  padding-bottom: 10px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
}
.searchbox {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 60%;
}
.image {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}
.price {
  width: 100%;
  text-align: center;
}
.name {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.results {
  width:100%;
}

.facets {
  width: 25%;
  display:inline-block;
  box-sizing: border-box;
  position: relative;

}
.products {
  width: 65%;
  position: absolute;
  display:inline-block;
  box-sizing: border-box;
}


em {
  background: cyan;
  font-style: normal;
}

.header {
  display: flex;
  align-items: center;
  min-height: 50px;
  padding: 0.5rem 1rem;
  background-image: linear-gradient(to right, #8e43e7, #00aeff);
  color: #fff;
  margin-bottom: 1rem;
}

.header a {
  color: #fff;
  text-decoration: none;
}

.header-title {
  font-size: 1.2rem;
  font-weight: normal;
}

.header-title::after {
  content: ' \25B8   ';
  padding: 0 0.5rem;
}

.header-subtitle {
  font-size: 1.2rem;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.search-panel {
  display: flex;
}

.search-panel__filters {
  flex: 1 1;
}

.search-panel__results {
  flex: 3 1;
}

.searchbox {
  margin-bottom: 2rem;
}

.pagination {
  margin: 2rem auto;
  text-align: center;
}

